<template>
    <div class="container">
        <div class="d-flex" style="flex: 1; position: fixed; top: 0; width: 100vw; z-index: 1000;">
            <div class="d-flex" style="flex: 1; max-height: 340px; background-color:#f5f5f5; border: 1px solid #ebebeb;">
                <div class="d-flex" style="padding: 15px; cursor: pointer;" @click="goToMain">
                    <div style="font-size: 24px; font-weight: bold; color: #bebebe;">Admin</div>
                </div>
                <div class="d-flex align-center header-text" style="cursor: pointer;" @click="onClickReset">고객관리</div>
                <div class="d-flex align-center header-text">ㅤㅤㅤ [-광고사]<span class="dropdown-icon">▼</span></div>
            </div>
        </div>
        <div class="d-flex flex-column" style="padding: 30px 30px 30px 15px; margin-top: 62px; flex: 1;">
            <div class="d-flex justify-end mb-2" v-if="tableData?.length == 0">
                 <label for="excelUpload" style="cursor: pointer; background-color: #007bff; color: white; padding: 6px 12px; border-radius: 4px;">
                    파일등록
                </label>
                <input
                    type="file"
                    id="excelUpload"
                    accept=".xlsx, .xls"
                    @change="onExcelUpload"
                    style="display: none;"
                />
            </div>

            <table class="custom-table">
                <thead>                   
                    <tr>
                        <th style="border-right: 1px solid #ddd; height: 100%;">연번</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">인입일시</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">고객명</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">상담번호</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">통신사</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">요금제</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">가입유형</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">할인유형</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">모델명</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">상담상태</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">설계상태</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">배송상태</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">개통상태</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">개통날짜</th>
                        <th style="border-right: 1px solid #ddd; height: 100%;">인입루트</th>
                        <th>비고</th>
                    </tr>
                </thead>
                <tbody style="border: 1px solid #ddd;">
                    <tr v-for="(item, index) in tableData" :key="index" @click="onClickOneItem(item)">
                        <td>
                             <div class="cell-content">{{ tableData.length - index }}</div>
                        </td>
                        <td>
                            <div class="cell-content mr-2">{{ formatExcelDateFormat(item['접수일시(insdt)']) || '' }}</div>
                            <div class="cell-content">{{ formatExcelTimeOnly(item['접수시간(inshms)']) || '' }}</div>
                        </td>
                        <td>
                            <div class="cell-content">{{ item['고객명(sang_name)'] || '' }}</div>
                        </td>
                        <td>
                            <div class="cell-content">{{ formatPhoneNumber(item['상담번호(sang_hp)']) || '' }}</div>
                        </td>
                        <td>
                            <div class="cell-content">{{ item['현재통신사(sang_tongsinsa)'] || '' }}</div>
                        </td>
                        <td>
                            <div class="cell-content">{{ item['요금제(yokumje)'] || '' }}</div>
                        </td>
                        <td>
                            <div class="cell-content">{{ item['가입유형(gaipgubun)'] || '' }}</div>
                        </td>
                        <td>
                            <div class="cell-content">{{ item['할인유형(dctype)'] || '' }}</div>
                        </td>
                        <td>
                            <div class="cell-content">{{ item['모델명(model)'] || '' }}</div>
                        </td>
                        <td>
                            <div class="d-flex flex-column">
                                <div class="cell-content">{{ item['상담상태(sangdam_status)'] || '' }}</div>
                                <div class="cell-content" style="color: #ff0099;">{{ formatExcelDateFormat(item['상담일자(sangdam_date)']) || '' }}</div>
                            </div>
                        </td>
                        <td>
                            <div class="d-flex flex-column">
                                <div class="cell-content">{{ item['설계상태(seolge_status)'] || '' }}</div>
                                <div class="cell-content" style="color: #ff0099;">{{ formatExcelDateFormat(item['설계일자(seolge_date)']) || '' }}</div>
                            </div>
                        </td>
                        <td>
                            <div class="d-flex flex-column">
                                <div class="cell-content">{{ item['배송상태(baesong_status)'] || '' }}</div>
                                <div class="cell-content" style="color: #ff0099;">{{ formatExcelDateFormat(item['배송일자(baesong_date)']) || '' }}</div>
                            </div>
                        </td>
                        <td>
                            <div class="d-flex flex-column">
                                <div class="cell-content">{{ item['개통상태(getong_status)'] || '' }}</div>
                                <div class="cell-content" style="color: #ff0099;">{{ formatExcelDateFormat(item['개통일자(getong_date)']) || '' }}</div>
                            </div>
                        </td>
                        <td>
                            <div class="cell-content">{{ item['개통날짜(getongdt)'] || '' }}</div>
                        </td>
                        <td>
                            <div class="cell-content">{{ item['인입루트(iniproute)'] || '' }}</div>
                        </td>
                        <td>
                            <div class="cell-content">{{ item['메모(request)'] || '' }}</div>
                        </td>
                    </tr>
                </tbody>
            </table>
            <div v-if="tableData?.length > 0" class="d-flex justify-center" style="margin: 30px 0px 0px 0px; ">
                <div class="d-flex" style="color: #fff; background-color: #337AB7; padding: 6px 12px; border-radius: 4px;">1</div>
            </div>
        </div>
       <div style="height: 57.14px; background-color: #f5f5f5;">
            <div class="d-flex" style="height: 100%; align-items: center; padding: 0 15px;"></div>
        </div>
        <handyDialog 
            v-if="handyState" 
            :handyState="handyState" 
            :data="selectedItem" 
            @close="handyState = false" 
        />
        <v-snackbar
              v-model="snackbar"
              timeout="1500"
              color="black"
              bottom
            >
              {{ snackbarMessage }}
              <template v-slot:action="{ attrs }">
                <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
                  닫기
                </v-btn>
              </template>
        </v-snackbar>
    </div>
</template>

<script>
import * as XLSX from 'xlsx';
import handyDialog from '@/dialog/handyDialog.vue';
    export default {
         components: {
            handyDialog
        },
        data(){
            return{
                tableData: [],
                selectedItem: null,
                handyState: false,
                snackbar: false,
                snackbarMessage: ''
            }
        },
        methods:{
            onExcelUpload(event) {
                const file = event.target.files[0]
                if (!file) {
                    alert("엑셀파일오류")
                    return
                }

                const reader = new FileReader()

                reader.onload = (e) => {
                    const data = new Uint8Array(e.target.result)
                    const workbook = XLSX.read(data, { type: 'array' })
                    const sheetName = workbook.SheetNames[0]
                    const worksheet = workbook.Sheets[sheetName]
                    const jsonData = XLSX.utils.sheet_to_json(worksheet)
                    jsonData.reverse()
                    this.tableData = jsonData
                }
                reader.readAsArrayBuffer(file)
            },
            formatExcelDateFormat(value) {
                if (!value) return '';

                // 문자열 형태의 8자리 숫자 (예: "20250501")
                if ((typeof value === 'string' || typeof value === 'number') && /^\d{8}$/.test(String(value))) {
                    const valStr = String(value);
                    const yyyy = valStr.slice(0, 4);
                    const mm = valStr.slice(4, 6);
                    const dd = valStr.slice(6, 8);
                    return `${yyyy}-${mm}-${dd}`;
                }

                // 숫자 형태의 엑셀 시리얼 값 (예: 45860)
                if (typeof value === 'number') {
                    const date = new Date(Math.round((value - 25569) * 86400 * 1000));
                    const yyyy = date.getUTCFullYear();
                    const mm = String(date.getUTCMonth() + 1).padStart(2, '0');
                    const dd = String(date.getUTCDate()).padStart(2, '0');
                    return `${yyyy}-${mm}-${dd}`;
                }

                return '';
            },
            formatExcelTimeOnly(value) {
                if (!value || value === '') return '0:00';

                if (typeof value === 'number' || /^\d+$/.test(value)) {
                    const str = value.toString().padStart(6, '0');
                    const hh = str.slice(0, 2);
                    const mi = str.slice(2, 4);
                    const ss = str.slice(4, 6);

                    const h = parseInt(hh, 10);
                    const m = parseInt(mi, 10);
                    const s = parseInt(ss, 10);

                    if (h > 0) {
                        return `${h}:${mi}:${ss}`;
                    } else if (m > 0) {
                        return `${m}:${ss}`;
                    } else if (s > 0) {
                        return `0:${ss}`;
                    } else {
                        return '0:00';
                    }
                }

                if (typeof value === 'string') return value;

                const date = new Date(Math.round((value - 25569) * 86400 * 1000));
                const hh = String(date.getUTCHours()).padStart(2, '0');
                const mi = String(date.getUTCMinutes()).padStart(2, '0');
                const ss = String(date.getUTCSeconds()).padStart(2, '0');

                return `${hh}:${mi}:${ss}`;
            },


            onClickOneItem(item) {
                this.selectedItem = item
                this.handyState = true
                console.log("클릭됨",item)
            },

            goToMain(){
                this.$router.push('/');
            },

            onClickReset(){
                if(this.tableData?.length > 0){
                    this.tableData = []
                    this.snackbar = true
                    this.snackbarMessage = '정보가 초기화 되었습니다.'
                    return
                }
            },
            formatPhoneNumber(value) {
                if (!value) return '';

                // 숫자만 추출
                let raw = String(value).replace(/[^0-9]/g, '');

                if (raw.length <= 3) {
                    return raw;
                } else if (raw.length <= 7) {
                    return raw.replace(/(\d{3})(\d{1,4})/, '$1-$2');
                } else if (raw.length <= 11) {
                    return raw.replace(/(\d{3})(\d{3,4})(\d{1,4})/, '$1-$2-$3');
                } else {
                    // 11자리 초과 시 잘라서 포맷
                    return raw.slice(0, 11).replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3');
                }
            }

        }
    }
</script>


<style lang="scss"  scoped>
    *{
        font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    }

    .container{
        width: 100dvw;
        min-width: 1400px;
        min-height: 100vh; /* 변경: 높이 고정 대신 최소 높이 */
        display: flex;
        flex-direction: column;
    }

    td{
         font-weight: 400;
    }

    .custom-table{
        width: 100%;
        border-collapse: collapse;
        border: 1px solid #ddd;
        overflow: auto;
    }

    .custom-table thead{
        background-color: #dff0d8;
    }

    .custom-table th{
        border-right: 1px solid #ddd;
        padding: 8px;
    }

    .custom-table tr{
        display: table-row;
        vertical-align: inherit;
        unicode-bidi: isolate;
        border-color: inherit;
    }

    .custom-table tbody tr:hover {
        background-color: #f0f8ff; /* 연한 파랑 배경 */
        cursor: pointer;
    }

    .custom-table td{
