        border: 1px solid #ddd;
        text-align: center;
        vertical-align: top;
        padding: 8px;
    }

    .cell-content{
        display: inline-block;
    }

    .custom-table tr:first-child td {
        border-top: 2px solid #ddd;
    }

    .body,th,td,div{
        font-size: 12px;
    }

    .header-text{
        padding: 15px;
        color: #777777;
    }

    .dropdown-icon{
        font-size: 8px;
        padding-left: 2px;
    }
</style>