
import Vue from 'vue';
import Router from 'vue-router';

import CustomerPages from '@/components/customerPages.vue';
import HandyPages from '@/components/handyPages.vue';

Vue.use(Router); 

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'CustomerPages',
      component: CustomerPages
    },
    {
      path: '/handy',
      name: 'HandyPages',
      component: HandyPages
    }
  ]
});
