<template>
  <v-dialog v-model="propsShow" :persistent="true">
    <div class="d-flex flex-column">
        <div class="d-flex flex-column" style="flex: 1;">
            <div class="d-flex justfy-space-between pa-4">
                <div class="d-flex" style="flex: 0.5;"></div>
                <div class="d-flex align-center justify-center" style="flex: 1; position: sticky; top: 0;">유저정보입력</div>
                <div class="d-flex justify-end" style="flex: 0.5;">
                    <div @click="onClickClose" class="clost-btn">X</div>
                </div>
            </div>
            <div class="d-flex flex-column pa-4" style="flex: 1;">
                <div class="d-flex flex-column mb-2" style="flex: 1;">
                    <div class="d-flex">KEY</div>
                    <input type="text" v-model="infoObj.key" placeholder="회원 KEY" style="border: 1px solid #989898; border-radius: 8px; padding: 4px;"/>
                </div>
                <div class="d-flex flex-column mb-2" style="flex: 1;">
                    <div class="d-flex">고객사</div>
                    <input type="text" v-model="infoObj.customer" placeholder="고객사" style="border: 1px solid #989898; border-radius: 8px; padding: 4px;" readonly/>
                </div>
                <div class="d-flex flex-column mb-2" style="flex: 1;">
                    <div class="d-flex">이름</div>
                    <input type="text" v-model="infoObj.name" placeholder="이름" style="border: 1px solid #989898; border-radius: 8px; padding: 4px;"/>
                </div>
                <div class="d-flex flex-column mb-2" style="flex: 1;">
                    <div class="d-flex">생년월일</div>
                    <input type="text" v-model="infoObj.birth" placeholder="생년월일 ex) 920101" style="border: 1px solid #989898; border-radius: 8px; padding: 4px;"/>
                </div>
                <div class="d-flex flex-column mb-2" style="flex: 1;">
                  <div class="d-flex space-between" style="flex: 1;">
                    <div class="d-flex" style="flex: 1;">연락처</div>
                  </div>
                    <input type="text" v-model="infoObj.phone" placeholder="연락처" @input="formatPhone" style="border: 1px solid #989898; border-radius: 8px; padding: 4px;"/>
                </div>
                <div class="d-flex flex-column mb-2" style="flex: 1;">
                  <div class="d-flex space-between" style="flex: 1;">
                    <div class="d-flex align-center" style="flex: 1;">통신사</div>
                    <div class="d-flex flex-column" style="flex: 1;">
                      <div class="d-flex justify-space-between" style="font-size: 14px;">1 = SK <span>2 = KT</span><span>3 = LG</span></div>
                      <div class="d-flex justify-space-between" style="font-size: 14px;">4 = SK알뜰<span>5 = KT알뜰</span><span>6 = LG알뜰</span></div>
                    </div>
                  </div>
                    <input type="text" v-model="infoObj.telecom" placeholder="통신사" style="border: 1px solid #989898; border-radius: 8px; padding: 4px;"/>
                </div>
                <div class="d-flex flex-column mb-2" style="flex: 1;">
                    <div class="d-flex">구매단말</div>
                    <input type="text" v-model="infoObj.emphysema" placeholder="구매단말" style="border: 1px solid #989898; border-radius: 8px; padding: 4px;"/>
                </div>
                 <div class="d-flex flex-column mb-2" style="flex: 1;">
                  <div class="d-flex space-between" style="flex: 1;">
                    <div class="d-flex" style="flex: 1;">구매일시</div>
                  </div>
                  <!-- <input type="text" v-model="infoObj.buyDate" placeholder="구매일시" style="border: 1px solid #989898; border-radius: 8px; padding: 4px;"/> -->
                   <DatePicker
                    v-model="infoObj.buyDate"
                    :format="'YYYY-MM-DD'"
                    value-type="format"
                    input-class="custom-input"
                    style="border: 1px solid #989898; border-radius: 8px; padding: 4px; width: 100%;"
                    placeholder="구매일시"
                    :lang="dateLocale"
                    :disabled-date="disableFutureDate"
                  />
                </div>
                <div class="d-flex flex-column mb-2" style="flex: 1;">
                    <div class="d-flex">가입일</div>
                    <!-- <input type="text" v-model="infoObj.date" placeholder="가입일 ex) 220101" style="border: 1px solid #989898; border-radius: 8px; padding: 4px;" @keyup.enter="onClickDone"/> -->
                      <DatePicker
                        v-model="infoObj.date"
                        :format="'YYYY-MM-DD'"
                        value-type="format"
                        input-class="custom-input"
                        style="border: 1px solid #989898; border-radius: 8px; padding: 4px; width: 100%;"
                        placeholder="가입일"
                        :lang="dateLocale"
                        :disabled-date="disableFutureDate"
                      />
                </div>                   
            </div>
            <div class="d-flex justify-center align-center" style="flex: 1;">
                <div class="d-flex" style="padding: 16px 50px 16px 50px; border-radius: 8px; background-color: #22a597; color: #fff; cursor: pointer;" @click="onClickDone">확인</div>
            </div>
            <v-snackbar
              v-model="snackbar"
              timeout="3000"
              color="black"
              bottom
            >
              {{ snackbarMessage }}
              <template v-slot:action="{ attrs }">
                <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
                  닫기
                </v-btn>
              </template>
            </v-snackbar>
        </div>
    </div>
  </v-dialog>
</template>

<script>
  import DatePicker from 'vue2-datepicker'
  import 'vue2-datepicker/index.css'
  import ko from 'vue2-datepicker/locale/ko';
  export default {
    components: {
      DatePicker
    },
    props: ['show'],
    data() {
      return {
        propsShow: this.show,
        infoObj: {key: '',customer: '우리WON마켓', name:'', birth:'', phone: '', buyDate: '', telecom: '',emphysema: '', date:''},
        dateLocale: ko,
        disableFutureDate(date) {
          const today = new Date();
          today.setHours(0, 0, 0, 0);
          return date > today; 
        },
        snackbar: false,
        snackbarMessage: ''
      }
    },
    methods:{
      onClickDone() {
        const emptyKey = Object.entries(this.infoObj).find(([key, value]) => {
          void key
          return value === '' || value === null || value === undefined
        })

        if (emptyKey) {
          const keyName = emptyKey[0]

          const keyMap = {
            key: '회원 KEY',
            customer: '고객사',
            name: '이름',
            birth: '생년월일',
            phone: '연락처',
            telecom: '통신사',
            emphysema: '구매단말',
            buyDate: '구매일시',
            date: '가입일',
          };

          this.snackbarMessage = `${keyMap[keyName] || keyName} 값을 입력하세요.`
          this.snackbar = true
          return
        }

        this.$emit('click-done', this.infoObj)
      },


      onClickClose(){
          this.$emit('close')
      },

      formatPhone(e) {
        // 숫자만 남기기
        let raw = e.target.value.replace(/[^0-9]/g, '');

        // 전화번호 패턴 적용
        if (raw.length <= 3) {
          this.infoObj.phone = raw;
        } else if (raw.length <= 7) {
          this.infoObj.phone = raw.replace(/(\d{3})(\d{1,4})/, '$1-$2');
        } else if (raw.length <= 11) {
          this.infoObj.phone = raw.replace(/(\d{3})(\d{4})(\d{1,4})/, '$1-$2-$3');
        } else {
          this.infoObj.phone = raw.slice(0, 11).replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3');
        }
      },
    },    
    watch: {
      show(val) {
        this.propsShow = val;
      },
      propsShow(val) {
        this.$emit('update:show', val);
      }
    },
  }
</script>

<style lang="scss" scoped>
    ::v-deep .v-dialog{
        background-color: #fff;
        max-width: 600px;
        height: 780px;
    }

    .clost-btn{
      width: 32px; 
      height: 32px; cursor: pointer;
      display: flex;
      justify-content: center;

      &:hover{
        transform: scale(1.1);
        color: red;
      }
    }
</style>
